// rem
// 
const baseSize = 75
//  rem 
function setRem () {
  //  1920
  const scale = document.documentElement.clientWidth / 750
  // Math.min(scale, 2) 2
  document.documentElement.style.fontSize = baseSize * Math.min(scale, 1) + 'px'
}
// 
setRem()
//  rem
window.onresize = function () {
  setRem()
}