<template>
    <div class="feedbackPage">
        <div class="content-list-box">
              <el-carousel  arrow='always' :interval="5000"  @change="onChange" :class="{elcarousehorizontal:showDetail}">
                <el-carousel-item>
                    <div class="list">
                        <img src="@/assets/img/userFeedback/user5.jpg" alt="" srcset="" :class="{active:showDetail}">
                        <div></div>
                        <div class="text-content" v-show="!showDetail">
                          <h3> &nbsp;&nbsp; 40&nbsp;&nbsp;6&nbsp;&nbsp;</h3>
                           <p>CAC</p>
                        </div>
                    </div>
                </el-carousel-item>
                <el-carousel-item>
                    <div class="list">
                        <img src="@/assets/img/userFeedback/user4.jpg" alt="" srcset="" :class="{active:showDetail}">
                         <div></div>
                        <div class="text-content" v-show="!showDetail">
                            <h3>&nbsp;&nbsp; 43 &nbsp;&nbsp; 8&nbsp;&nbsp; 5</h3>
                            <p>"99%"</p>
                        </div>
                    </div>
                </el-carousel-item>
                <el-carousel-item>
                   <div class="list">
                        <img src="@/assets/img/userFeedback/user6.jpg" alt="" srcset="" :class="{active:showDetail}">
                        <div></div>
                        <div class="text-content" v-show="!showDetail">
                           <h3>&nbsp;&nbsp;68&nbsp;&nbsp;&nbsp;&nbsp; </h3>
                            <p></p>
                        </div>
                    </div>
                </el-carousel-item>
             </el-carousel>
            
            <div class="lookDetail">
                <button @click="lookDetail()"> 
                    <span v-if="!showDetail"></span>
                    <span v-if="showDetail"></span>
                </button>
            </div>

            <div v-if="showDetail">
                <div v-for="(item,index) in listData" :key="index" class="expert-text-list">
                    <div class="content-detail" v-if="index == current">
                        <div>
                            <span></span>
                            <p v-html="item.text1"></p>
                        </div>
                        <div>
                            <span></span>
                            <p v-html="item.text2"></p>
                        </div>
                        <div>
                            <span></span>
                            <p v-html="item.text3"></p>
                        </div>
                        <div>
                            <span></span>
                            <p v-html="item.text4"></p>
                        </div>
                        <div v-if="item.text5">
                            <span></span>
                            <p >{{item.text5}}</p>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>

        <!--  -->
        <div class="Detection-flow-chart">
            <img :src="imgSrc" alt="" srcset="">
        </div>

    </div>
</template>
<script>
import Vue from 'vue';
import { Swipe, SwipeItem } from 'vant';
import { Icon } from 'vant';

Vue.use(Icon);
Vue.use(Swipe);
Vue.use(SwipeItem)
export default {
   name:'',
   data(){
       return{
           showDetail:false,
           current:0,
           listData:[{
               text1:'20208.5 x 11.1mm ',
               text2:'2020<sup></sup>',
               text3:'',
               text4:'',
           },{
               text1:'20176.3 x 4.5mm',
               text2:'202088.6 x 7.8mmCT',
               text3:'20209<sup></sup>',
               text4:'',
           },{
               text1:'2019,CT8.4 x 7.1mm',
               text2:'',
               text3:'',
               text4:'20205',
               text5:''
           }],
           imgSrc:''
       }
   },
   mounted(){
       this.getProductDetailList('productD2','h5');//h5
   },
   methods:{
        lookDetail:function(){
            this.showDetail = !this.showDetail
        },
        onChange(index) {
            this.current = index;
            console.log(index)
        },
        getProductDetailList(pagename,browsertype){
            let that = this;
            that.axios.get('photolist',{
            params: {
                pagename: pagename,
                browsertype:browsertype
            }
            })
            .then(function (res) {
                console.log(res.data);
                if(res.status == 200 ){
                    if(res.data.data && res.data.data.length>0){
                        that.imgSrc = res.data.data.pop().photourl;
                    };
                }
            }) 
        },
        PreviousStep(){
            this.$refs.nop.prev();
        },
        nextStep(){
            this.$refs.nop.next();
        },

   }
}
</script>
