<template>
    <div class="resultPage">
        <div class="resultPage-box">
            <header>
                <p></p>   
            </header>
            <div class="result-type">
                
            </div>
            <div class="title" v-for="item in resultArr" :key="item.index">
                <button>{{item.type}}</button>
            </div>
            <div class="type-box">
                <img src="@/assets/img/result/type1.png"  v-if="type==1 || type==2" alt="" srcset="">
                <img src="@/assets/img/result/type2.png"  v-if="type==3 " alt="" srcset="">
                <img src="@/assets/img/result/type3.png"  v-if="type==5 || type==4" alt="" srcset=""> 
            </div>
            <div class="content-list" v-for="item in resultArr" :key="item.index">
                <div class="title">{{item.title}}</div>
                <p>{{item.tip}}</p>
                <p v-html="item.content"></p>
            </div>

            <div class="reference-box">
                <div class="reference-title"></div>
                <p>1401 2040020400  &lt;15 23 4</p>
            </div>
        </div>
    </div>
</template>
<script>
import qs from 'qs'
export default {
    data(){
        return{
            resultArr:[],
            resultArr1:[{
                type:'',
                // title:'',
                // tip:'',
                content:'20184 mm12>5mmCT'
            }],

            resultArr2:[{
                type:'',
                // title:'',
                // tip:'',
                content:'2018'
            }],

            resultArr3:[{//40
                type:'',
                title:'',
                // tip:'',
                content:'<sup></sup>'
            }],

            resultArr4:[{//40-60
                type:'',
                title:'',
                // tip:'',
                content:'<sup></sup>'
            }],

            resultArr5:[{//60
                type:'',
                title:'',
                // tip:'',
                content:'<sup></sup>'
            }],
            type:''
        }
    },
    mounted(){
        var query=this.$route.query;
        let key = query.id;
        let that  = this;
        this.type = query.id;
        switch (key) {
            case '1':
                that.resultArr = that.resultArr1; 
                that.resurtSubmit(that.resultArr);  
                break;
            case '2':
                that.resultArr = that.resultArr2;
                that.resurtSubmit(that.resultArr);
                break;
            case '3':
                that.resultArr = that.resultArr3;
                that.resurtSubmit(that.resultArr);
                break;
            case '4':
                that.resultArr = that.resultArr4;
                that.resurtSubmit(that.resultArr);
                break;
            case '5':
                that.resultArr = that.resultArr5;
                that.resurtSubmit(that.resultArr);
                break;
            default:
                break;
        };
    },
    methods:{
        goBack(){
            this.$router.go(-1);
        },
        //
        resurtSubmit(row){
            console.log(row);
            let that  = this;
            let mobile = localStorage.getItem('tel');
            var promise = {}
            row.forEach(element => {
                promise = {
                    mobile:mobile,
                    leixing:element.type,
                    result:element.content,
                    q1:element.title,
                    q2:element.tip,
                    q3:'',
                    q4:'',
                };
            });
            that.axios.post('zicepost', qs.stringify(promise))
            .then(function (res) {
                console.log(res);
                if(res.status == 200 ){
                    if(res.data && res.data.data){
                        console.log('!');
                    }
                }else{
                    that.$message({
                    message: '',
                    type: 'warning'
                    });
                }
            });
        },
    }
}
</script>
